body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --color-bg: hsl(0, 41%, 15%);
  --color-primary: hsl(297, 64%, 28%);
  --color-secondary: hsl(327, 100%, 47%);
  --color-tertiary: hsl(341, 85%, 74%);
  --color-tertiary-hover: hsl(341, 85%, 60%);
  --color-bg-alt: hsl(203, 82%, 33%);
  --color-primary-alt: hsl(216, 84%, 47%);
  --color-secondary-alt: hsl(204, 100%, 64%);
  --color-tertiary-alt: hsl(197, 100%, 94%);
  --color-tertiary-alt-hover: hsl(197, 100%, 70%);

}


.bodyalt {
  background-color: hsl(203, 82%, 33%);
  background-color: var(--color-bg-alt);
}

.App {
  display: inline-block;
  text-align: center;
  min-height: 100vh;
  min-width: 100vw;
  background-color: hsl(0, 41%, 15%);
  background-color: var(--color-bg);
}

.Appalt {
  background-color: hsl(203, 82%, 33%);
  background-color: var(--color-bg-alt);
}

h1 {
  font-size: 3.25rem;
  color: hsl(327, 100%, 47%);
  color: var(--color-secondary)
}

.h1alt {
  color: hsl(204, 100%, 64%);
  color: var(--color-secondary-alt)
}

h3 {
  font-size: 2.5rem;
  color: hsl(341, 85%, 74%);
  color: var(--color-tertiary)
}

.h3alt {
  color: hsl(197, 100%, 94%);
  color: var(--color-tertiary-alt)
}

input {
  font-size: 2rem;
}

.gradeContainer {
  display: flex;
  justify-content: center;
  align-items: center;
}

.gradeDisplay {
  margin-left: 5px;
  margin-right: 5px;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}
.switchWrapper {
    --size: 2.5rem;
    position: absolute;
    right: 5%;
    top: 1%;
    width: calc(var(--size) * 2.2);
    height: var(--size);
    background-color: var(--color-bg-alt);
    border-radius: calc(var(--size) / 2);
    transition: .5s;
}

.switchWrapper-alt {
    background-color: var(--color-bg);
}

.switchTrigger {
    position: relative;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    height: var(--size);
    width: var(--size);
    /* background-color: var(--color-tertiary-alt); */
    border-radius: 50%;
    transition: .5s;
    cursor: pointer;
}
